body {
  overflow-x: hidden;
}

.header {
  position: relative;
  z-index: 999;
  padding: 25px 0;
}
.header__search {
  position: relative;
  max-width: 620px;
  width: 100%;
}
.header__search-head {
  color: #89817c;
}
.header__search-body {
  position: absolute;
  top: 60px;
  left: 0;
  width: 100%;
  background-color: #fff;
  padding: 12px 36px;
  border-radius: 8px;
  box-shadow: 0px 20px 60px 0px rgba(69, 63, 58, 0.3);
  visibility: hidden;
  opacity: 0;
  transform: translateY(-50%) scaleY(0);
  z-index: 10;
  transition: all 0.3s;
}
.header__search-body.active {
  visibility: visible;
  opacity: 1;
  transform: translateY(0) scaleY(1);
}
.header__search-text {
  color: #453f3a;
  font-weight: 400;
}
.header__search-product {
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 24px;
  font-size: 14px;
}
.header__search-product + .header__search-product {
  margin-top: 11px;
}
.header__search-product-left {
  display: flex;
  align-items: center;
  overflow: hidden;
}
.header__search-product-img {
  min-width: 64px;
  max-width: 64px;
  height: 88px;
  max-height: 88px;
}
.header__search-product-img:hover img {
  transform: scale(1.2);
}
.header__search-product-img img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  transition: all 0.3s;
}
.header__search-product-info {
  margin-left: 24px;
  overflow: hidden;
}
.header__search-product-title {
  display: -webkit-box;
  color: #453f3a;
  line-height: 130%;
  overflow: hidden;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 4;
  transition: all 0.3s;
}
.header__search-product-title:hover {
  color: #a46c46;
}
@media (max-width: 1300px) {
  .header__search-body {
    padding: 12px 20px;
  }
  .header__search-product-left {
    width: 100%;
  }
}
@media (max-width: 1200px) {
  .header__search-product {
    column-gap: 16px;
  }
}
@media (max-width: 720px) {
  .header__search-body {
    padding: 12px;
  }
  .header__search-product {
    font-size: 12px;
  }
  .header__search-product-info {
    margin-left: 12px;
  }
}
@media (max-width: 600px) {
  .header__search-product-img {
    min-width: 45px;
    max-width: 45px;
    height: 60px;
    max-height: 60px;
  }
}

.header_top_panel__block {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding-bottom: 10px;
  border-bottom: 1px solid #ebeae8;
}
.header_top_panel_info__block {
  display: flex;
}
.header_top_panel_clock {
  align-items: flex-start;
  display: flex;
}
.header_top_panel_clock img {
  margin-right: 15px;
  margin-top: 4px;
}
.header_top_panel_clock_info {
  display: flex;
  flex-direction: column;
  font-size: 20px;
  line-height: 22px;
}
.header_top_panel_clock_subtext {
  font-size: 14px;
}
.header_top_panel_nav__block {
  width: 100%;
  max-width: 600px;
}
.header_top_panel_nav_list {
  width: 100%;
  padding: 0;
  margin: 0;
  display: flex;
  align-items: center;
  justify-content: end;
}
.header_top_panel_nav_list li {
  display: flex;
  width: auto;
  margin-left: 30px;
}
.header_top_panel_nav_list li a {
  display: flex;
  width: 100%;
}
.header_top_panel_nav_link {
  font-size: 16px;
  line-height: 21px;
}

.header_bottom_panel__block {
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  background: #fff;
  padding: 15px 7% 15px;
  border-bottom: 1px solid #ebeae8;
  transition: transform 0.3s ease-out;
}
.header_bottom_panel__block.fixed {
  position: fixed;
  top: 0;
}
.header_bottom_panel_logo {
  cursor: pointer;
  margin-right: 30px;
}
.header_bottom_panel_logo .white {
  display: none;
}
.header_bottom_panel_catalog {
  position: relative;
  transition: all 0.2s;
  cursor: pointer;
  display: flex;
  padding: 0px 20px;
  align-items: center;
  justify-content: center;
  min-width: 140px;
  width: 140px;
  height: 50px;
  background: #a46c46;
  border-radius: 8px;
  margin-left: 30px;
  margin-right: 30px;
}
.header_bottom_panel_catalog_label {
  font-size: 16px;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: 30px;
}
.header_bottom_panel_catalog_lines {
  transform: scale(0.1);
  position: relative;
  margin-right: 70px;
}
.header_bottom_panel_catalog_lines svg {
  fill-rule: evenodd;
  clip-rule: evenodd;
  stroke-linecap: round;
  stroke-linejoin: round;
  stroke-miterlimit: 1.5;
  cursor: pointer;
}
.header_bottom_panel_catalog_lines svg path {
  fill: none;
  stroke: white;
  stroke-width: 2px;
}
.header_bottom_panel_catalog_lines svg + svg {
  margin-left: 1.5rem;
}
.header_bottom_panel_catalog_lines h1 {
  font-size: 2.5rem;
  text-transform: uppercase;
  letter-spacing: 5px;
  text-align: center;
  margin-bottom: 2rem;
}
.header_bottom_panel_catalog_lines .icons {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
.header_bottom_panel_catalog_lines #top,
.header_bottom_panel_catalog_lines #bottom {
  stroke-dasharray: 30, 75.39;
  transition: all 0.6s cubic-bezier(0.6, 0.33, 0.67, 1.29);
}
.header_bottom_panel_catalog_lines .rocket #top {
  stroke-dasharray: 30, 88;
}
.header_bottom_panel_catalog_bg {
  pointer-events: none;
  z-index: -1;
  transition: all 0.2s;
  position: fixed;
  left: 0;
  top: 95px;
  opacity: 0;
  width: 100vw;
  height: 100vh;
  background: linear-gradient(180deg, rgba(69, 63, 58, 0.7) 0%, rgba(69, 63, 58, 0.6) 100%);
}
.header_bottom_panel_catalog_bg.active {
  pointer-events: initial;
  z-index: 1;
  opacity: 12313;
}
.header_bottom_panel_catalog_bg.fixed {
  top: 80px !important;
  opacity: 1;
}
.header_bottom_panel_catalog.active svg #top,
.header_bottom_panel_catalog.active svg #bottom {
  stroke-dasharray: 75.39;
  stroke-dashoffset: -60;
}
.header_bottom_panel_catalog_content {
  pointer-events: none;
  transition: all 0.2s;
  position: fixed;
  display: flex;
  flex-wrap: wrap;
  opacity: 0;
  z-index: -1;
  left: 50%;
  top: 55px;
  width: 86%;
  transform: translateX(-50%);
  padding: 20px;
  padding-bottom: 10px;
  background: #ffffff;
  border-radius: 0px 0px 10px 10px;
}
.header_bottom_panel_catalog_content.active {
  opacity: 1;
  top: 95px;
  z-index: 9;
  pointer-events: initial;
}
.header_bottom_panel_catalog_content.fixed {
  top: 80px;
}
.header_bottom_panel_catalog_content_item {
  position: relative;
  left: 0;
  transition: all 0.2s;
  font-size: 20px;
  width: calc(25% - 20px);
  min-width: 330px;
  margin: 0px 10px;
  margin-bottom: 25px;
  cursor: pointer;
}
.header_bottom_panel_catalog_content_item:hover {
  left: 8px;
  color: #a46c46;
}
.header_bottom_panel_left_content {
  display: flex;
  align-items: center;
  width: 100%;
}
.header_bottom_panel_search {
  position: relative;
  display: flex;
  align-items: center;
  width: 100%;
  height: 50px;
  background: #ebeae8;
  border-radius: 8px;
}
.header_bottom_panel_search_input {
  outline: none;
  border: unset;
  padding: 0px 20px;
  background: transparent;
  height: 100%;
  width: calc(100% - 50px);
}
.header_bottom_panel_search_label {
  position: relative;
  z-index: 9;
}
.header_bottom_panel_search_icon {
  display: flex;
  position: absolute;
  transform: translateY(-50%);
  top: 50%;
  right: 30px;
}
.header_bottom_panel_nav {
  display: flex;
  align-items: center;
}
.header_bottom_panel_item {
  display: flex;
  align-items: center;
  margin-left: 40px;
}
.header_bottom_panel_item_icon {
  position: relative;
  display: flex;
  align-items: center;
  margin-right: 15px;
}
.header_bottom_panel_item_icon span {
  padding-left: 6px;
  font-size: 16px;
}
.header_bottom_panel_item_label {
  font-size: 18px;
}

.header_widget_wrapper {
  width: 100%;
}
.header_widget_wrapper .header_bottom_panel_cart {
  display: none;
}

.header_content__block.mobile {
  display: none;
}

.catalog_bg_right {
  display: none;
}

.header_close_menu {
  display: none;
}

@media (max-width: 1600px) {
  .header_bottom_panel_search {
    width: 100%;
  }
}
@media (max-width: 1100px) {
  .header_bottom_panel_catalog {
    margin: 0px 15px;
  }
  .header_bottom_panel_logo {
    margin-right: 15px;
  }
  .header_bottom_panel_item {
    margin-left: 20px;
  }
}
@media (max-width: 1024px) {
  .header_info_title {
    font-size: 18px;
    margin-bottom: 20px;
  }
  .header_info_link {
    display: flex;
    font-size: 16px;
    margin-bottom: 10px;
  }
  .header_info__block {
    display: flex;
    justify-content: space-between;
  }
  .header_payment_info {
    display: flex;
    align-items: center;
    margin-top: 30px;
    justify-content: center;
  }
  .header_payment_info img {
    margin: 0px 10px;
  }
  .header_catalog_scroll {
    overflow-y: auto;
    height: 100%;
    max-height: 200px;
    overflow-x: hidden;
    padding-right: 6px;
    margin-top: 25px;
    margin-bottom: 60px;
  }
  .header_mob_icon {
    display: flex;
    align-items: center;
    margin-right: 15px;
  }
  .header_mob_icon__block {
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  .header_mob_icon_item {
    display: flex;
    align-items: center;
  }
  .header_mob_icon_text {
    font-size: 12px;
  }
  .header_mob_icon_count {
    font-size: 12px;
    margin-left: 4px;
  }
  .header_content__block {
    pointer-events: none;
    transition: all 0.2s;
    position: fixed;
    top: 0;
    z-index: 9999;
    opacity: 0;
  }
  .header_content__block.mobile {
    width: 100%;
    left: 0;
    padding: 0px 20px;
    display: flex;
    flex-direction: column;
  }
  .header_content__block.active {
    pointer-events: initial;
    top: 90px;
    opacity: 1;
  }
  .header_close_menu {
    display: flex;
  }
  .catalog_bg_right {
    display: flex;
  }
  .header_bottom_panel_catalog_bg {
    top: 58px !important;
    background: #89817c;
    width: 51%;
    transition: all 0.3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
    border-top: 1px solid #fff;
    border-right: 1px solid #89817c;
    border-left: 1px solid #89817c;
  }
  .header_bottom_panel_catalog_bg.catalog_bg_left {
    right: unset;
    left: -100%;
  }
  .header_bottom_panel_catalog_bg.catalog_bg_left.active {
    left: 0;
  }
  .header_bottom_panel_catalog_bg.catalog_bg_right {
    left: unset;
    right: -100%;
  }
  .header_bottom_panel_catalog_bg.catalog_bg_right.active {
    right: 0;
  }
  .header_bottom_panel_catalog_bg.active, .header_bottom_panel_catalog_bg.fixed {
    top: 58px !important;
    opacity: 1;
  }
  .header {
    padding: 0;
    height: 0;
  }
  .header_widget_wrapper {
    position: relative;
    display: flex;
    align-items: center;
    right: 100px;
    max-width: 620px;
  }
  .header_widget_wrapper .header_bottom_panel_cart {
    display: flex;
  }
  .header_bottom_panel__block {
    position: fixed;
    padding: 12px 20px;
  }
  .header_top_panel__block {
    display: none;
  }
  .header_bottom_panel_left_content {
    justify-content: space-between;
  }
  .header_bottom_panel_nav {
    display: none;
  }
  .header_bottom_panel_catalog {
    position: absolute;
    right: 20px;
    height: 34px;
    margin: 0;
    width: 83px;
    min-width: 83px;
  }
  .header_bottom_panel_catalog .header_bottom_panel_catalog_label {
    font-size: 12px;
    right: 11px;
  }
  .header_bottom_panel_catalog .header_bottom_panel_catalog_lines {
    transform: scale(0.05);
    position: relative;
    margin-right: 50px;
  }
  .header_bottom_panel_catalog .header_bottom_panel_catalog_content {
    display: none;
  }
  .header_bottom_panel_search_icon {
    right: 12px;
  }
  .header_bottom_panel_search {
    transition: all 0.2s;
    height: 34px;
    background: transparent;
  }
  .header_bottom_panel_search input {
    transition: all 0.2s;
    width: 1px;
    opacity: 0;
  }
  .header_bottom_panel_search.active {
    background: #ebeae8;
  }
  .header_bottom_panel_search.active input {
    padding: 0px 40px 0px 10px;
    width: 100%;
    opacity: 1;
  }
  .header_bottom_panel_logo {
    width: 36px;
  }
  .header_bottom_panel_logo img {
    width: 100%;
  }
  .header_close_menu {
    position: absolute;
    display: flex;
    align-items: center;
    justify-content: center;
    background: #fffcf2;
    border-radius: 8px;
    height: 34px;
    margin: 0;
    width: 83px;
    top: -30px;
    right: 20px;
  }
  .header_close_menu_title {
    font-size: 12px;
  }
  .header_close_menu .cross {
    position: relative;
    height: 10px;
    width: 10px;
    margin-right: 0px;
  }
  .header_close_menu .cross::before,
  .header_close_menu .cross::after {
    content: "";
    position: absolute;
    height: 100%;
    width: 1px;
    background-color: #a46c46;
  }
  .header_close_menu .cross::before {
    transform: rotate(45deg);
  }
  .header_close_menu .cross::after {
    transform: rotate(-45deg);
  }
  .header .header_bottom_panel__block {
    transition: all 0.2s;
    transition-delay: 0.1s;
    top: 0;
  }
  .header .animate_header_1 {
    transition: all 0.2s;
    top: 0;
  }
  .header .animate_header_2 {
    position: relative;
    transition: all 0.2s;
    transition-delay: 0.1s;
    top: 0;
  }
  .header .animate_header_3 {
    transition: all 0.2s;
    transition-delay: 0.3s;
  }
  .header .animate_header_3 .header_bottom_panel_catalog_label,
  .header .animate_header_3 .header_bottom_panel_catalog_lines {
    transition: all 0.2s;
    transition-delay: 0.3s;
    opacity: 1;
  }
  .header .animate_header_3 {
    opacity: 1;
  }
  .header .animate_header_4 {
    position: relative;
    transition: all 0.2s;
    transition-delay: 0.2s;
  }
  .header .animate_header_4 img {
    top: 50%;
    transform: translateY(-50%);
  }
  .header .animate_header_4 .black {
    position: absolute;
    transition: all 0.2s;
    transition-delay: 0.2s;
    opacity: 1;
    left: 0px;
  }
  .header .animate_header_4 .white {
    display: flex;
    position: absolute;
    transition: all 0.2s;
    transition-delay: 0.2s;
    opacity: 0;
    left: -40px;
  }
  .header .animate_header_5 {
    transition: all 0.2s;
    transition-delay: 0.2s;
    opacity: 0;
  }
  .header.active .header_bottom_panel__block {
    background: #89817c;
  }
  .header.active .animate_header_1, .header.active .animate_header_2 {
    top: -60px;
  }
  .header.active .animate_header_3 {
    background: transparent;
  }
  .header.active .animate_header_3 .header_bottom_panel_catalog_label,
  .header.active .animate_header_3 .header_bottom_panel_catalog_lines {
    opacity: 0;
  }
  .header.active .animate_header_4 .black {
    left: -60px;
    opacity: 0;
  }
  .header.active .animate_header_4 .white {
    left: 0;
    opacity: 1;
  }
  .header.active .animate_header_5 {
    top: 10px;
    opacity: 1;
  }
}
@media (max-width: 820px) {
  .header_widget_wrapper {
    max-width: calc(100% - 176px);
  }
}
@media (max-width: 450px) {
  .header_widget_wrapper {
    right: 45px;
    max-width: calc(100% - 100px);
  }
  .header_bottom_panel_cart {
    margin-left: 15px;
  }
  .header_bottom_panel_catalog {
    width: 40px;
    min-width: 40px;
  }
  .header_bottom_panel_catalog_lines {
    margin: 0 !important;
  }
  .header_bottom_panel_catalog_label {
    font-size: 0 !important;
  }
}