.modal {
  position: relative;
  display: flex;
  align-items: center;
  max-width: 680px;
  width: 100%;
  height: 100%;
  background-color: #fff;
  margin: 0 0 0 auto;
  transform: translateX(100%);
  transition: all 0.35s ease-in-out;
}
.modal.active {
  transform: translateX(0);
}
.modal-overlay {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(180deg, rgba(69, 63, 58, 0.7) 0%, rgba(69, 63, 58, 0.6) 100%);
  visibility: hidden;
  opacity: 0;
  transition: all 0.3s ease-in-out;
  z-index: 9999;
}
.modal-overlay.active {
  visibility: visible;
  opacity: 1;
}
.modal__close-btn {
  position: absolute;
  top: 70px;
  left: -40px;
  max-width: 80px;
  max-height: 80px;
  background-color: #ebeae8;
  padding: 24px;
  border-radius: 100%;
  transition: all 0.25s;
}
.modal__close-btn img {
  width: 32px;
  height: 32px;
  transition: all 0.35s;
}
.modal__close-btn:hover {
  transform: scale(1.1);
  filter: brightness(0.85);
}
.modal__close-btn:hover img {
  transform: scale(1.5);
}
.modal__close-btn:active {
  transform: translateY(5px) scale(1.1);
  filter: brightness(0.95);
}
.modal__content {
  width: 100%;
  max-height: 100%;
  padding: 20px 0 20px 125px;
  overflow: auto;
}
.modal__content button {
  width: 100%;
}
.modal__logo {
  max-width: 60px;
  max-height: 60px;
  margin-bottom: 40px;
}
.modal__logo img {
  width: 100%;
  height: 100%;
}
.modal__title {
  font-family: "Futura PT Medium", sans-serif;
  color: #453f3a;
  font-size: 36px;
  font-weight: 500;
}
.modal__field {
  margin-top: 20px;
}
.modal__field input {
  width: 100%;
}
.modal__text {
  color: #89817c;
  font-size: 20px;
}
@media (max-width: 1024px) {
  .modal__title {
    font-size: 30px;
  }
  .modal__text {
    font-size: 18px;
  }
}
@media (max-width: 900px) {
  .modal {
    max-width: 550px;
  }
  .modal__content {
    padding-left: 85px;
  }
}
@media (max-width: 720px) {
  .modal {
    max-width: none;
    padding: 0 0 124px;
    transform: translateY(100%);
  }
  .modal.active {
    transform: translateY(0);
  }
  .modal__close-btn {
    top: auto;
    bottom: 24px;
    left: 50%;
    transform: translateX(-50%);
  }
  .modal__content {
    padding: 20px;
  }
  .modal__title {
    font-size: 24px;
  }
  .modal__field {
    margin-top: 24px;
  }
  .modal__field + .modal__field {
    margin-top: 20px;
  }
  .modal__text {
    font-size: 16px;
  }
}