.password-reset {
  height: calc(100vh - 105px);
  min-height: 600px;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 60px 20px;
  background: linear-gradient(135deg, #fafafa 0%, #f5f5f5 100%);
}
@media (max-width: 768px) {
  .password-reset {
    height: calc(100vh - 90px);
    min-height: 500px;
    padding: 40px 20px;
  }
}
.password-reset__wrapper {
  width: 100%;
  max-width: 1200px;
  height: 100%;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: center;
}
.password-reset__container {
  width: 100%;
  max-width: 480px;
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.08);
  padding: 48px 40px;
  animation: fadeInUp 0.5s ease-out;
}
@media (max-width: 768px) {
  .password-reset__container {
    padding: 36px 24px;
    max-width: 100%;
  }
}
.password-reset__header {
  text-align: center;
  margin-bottom: 36px;
}
.password-reset__title {
  font-size: 32px;
  font-weight: 700;
  color: #1a1a1a;
  margin-bottom: 12px;
  line-height: 1.2;
}
@media (max-width: 768px) {
  .password-reset__title {
    font-size: 26px;
  }
}
.password-reset__subtitle {
  font-size: 15px;
  line-height: 1.6;
  color: #666666;
  max-width: 380px;
  margin: 0 auto;
}
@media (max-width: 768px) {
  .password-reset__subtitle {
    font-size: 14px;
  }
}
.password-reset__form {
  margin-bottom: 28px;
}
.password-reset__form-field {
  margin-bottom: 24px;
}
.password-reset__input {
  width: 100%;
  height: 52px;
  font-size: 16px;
  padding: 0 18px;
  border: 1.5px solid #e0e0e0;
  border-radius: 8px;
  transition: all 0.3s ease;
  background: #fafafa;
}
.password-reset__input:focus {
  background: #ffffff;
  border-color: #a46c46;
  outline: none;
  box-shadow: 0 0 0 3px rgba(164, 108, 70, 0.1);
}
.password-reset__input::placeholder {
  color: #999999;
}
@media (max-width: 768px) {
  .password-reset__input {
    height: 48px;
    font-size: 15px;
  }
}
.password-reset__submit-btn {
  width: 100%;
  height: 52px;
  font-size: 16px;
  font-weight: 600;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s ease;
  text-transform: none;
  letter-spacing: 0.3px;
}
.password-reset__submit-btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(164, 108, 70, 0.3);
}
.password-reset__submit-btn:active {
  transform: translateY(0);
}
@media (max-width: 768px) {
  .password-reset__submit-btn {
    height: 48px;
    font-size: 15px;
  }
}
.password-reset__footer {
  text-align: center;
  padding-top: 24px;
  border-top: 1px solid #eeeeee;
}
.password-reset__back-link {
  display: inline-flex;
  align-items: center;
  gap: 8px;
  font-size: 15px;
  color: #666666;
  text-decoration: none;
  transition: all 0.3s ease;
  font-weight: 500;
}
.password-reset__back-link svg {
  transition: transform 0.3s ease;
}
.password-reset__back-link:hover {
  color: #a46c46;
}
.password-reset__back-link:hover svg {
  transform: translateX(-4px);
}
@media (max-width: 768px) {
  .password-reset__back-link {
    font-size: 14px;
  }
}

@keyframes fadeInUp {
  from {
    opacity: 0;
    transform: translateY(30px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.validation_container {
  position: relative;
}
.validation_container .validation_error {
  position: absolute;
  bottom: -22px;
  left: 0;
  font-size: 13px;
  color: #e74c3c;
  animation: slideDown 0.3s ease-out;
}

@keyframes slideDown {
  from {
    opacity: 0;
    transform: translateY(-8px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.validation_input.error {
  border-color: #e74c3c !important;
  background: #fff5f5 !important;
}
.validation_input.error:focus {
  box-shadow: 0 0 0 3px rgba(231, 76, 60, 0.1) !important;
}

.password-reset__success {
  text-align: center;
  padding: 20px;
  background: #d4edda;
  border: 1px solid #c3e6cb;
  border-radius: 8px;
  color: #155724;
  margin-bottom: 24px;
  animation: fadeInUp 0.5s ease-out;
}

.password-reset__error {
  text-align: center;
  padding: 20px;
  background: #f8d7da;
  border: 1px solid #f5c6cb;
  border-radius: 8px;
  color: #721c24;
  margin-bottom: 24px;
  animation: fadeInUp 0.5s ease-out;
}