.footer {
  background-color: #f4f3ee;
  background-repeat: no-repeat;
  background-position: center;
}
.footer__wrapper {
  display: flex;
  justify-content: space-between;
  padding: 5% 0;
}
.footer_form {
  width: 20%;
}
.footer_form_title {
  margin-bottom: 20px;
}
.footer_form_input {
  margin: 30px 0 12px 0;
}
.footer .subscription-form__subscribe-btn {
  max-width: 265px;
  width: 100%;
  margin-top: 20px;
}
.footer__right {
  display: flex;
}
.footer__column {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.footer__column:first-child {
  margin-right: 100px;
}
.footer__column a img {
  margin-right: 20px;
}
.footer__links {
  display: flex;
  flex-direction: column;
}
.footer__links a,
.footer__links button {
  color: #89817c;
  margin-bottom: 30px;
}
.footer__links a:hover,
.footer__links button:hover {
  color: #a46c46;
}
.footer__links button {
  text-align: left;
}
.footer .accept {
  color: #89817c;
  border-bottom: 1px solid #89817c;
  font-size: 14px;
}
@media (max-width: 1024px) {
  .footer {
    background-image: none !important;
  }
  .footer__wrapper {
    justify-content: center;
    padding: 20px 0;
  }
  .footer_form {
    display: none;
  }
  .footer__column:first-child {
    margin: 0;
  }
  .footer__column + .footer__column {
    display: none;
  }
  .footer__column a {
    font-size: 14px;
  }
  .footer__links {
    display: none;
  }
}
@media (max-width: 450px) {
  .footer__column a {
    font-size: 12px;
  }
}