.loader {
  width: 50px;
  height: 50px;
  border: 5px solid #fffcf2;
  border-top: 4px solid #a46c46;
  border-radius: 100%;
  animation: spin 1s linear infinite;
}
@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.modal_loading__block, .modal_accept__block, .modal_bad__block {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  background: rgba(0, 0, 0, 0.5);
  visibility: hidden;
  opacity: 0;
  z-index: 99999;
  width: 200px;
  height: 200px;
  border-radius: 16px;
}
.modal_loading__block.active, .modal_accept__block.active, .modal_bad__block.active {
  transition: all 0.2s;
  visibility: visible;
  opacity: 1;
}
.modal_accept__block, .modal_bad__block {
  flex-direction: column;
  gap: 10px;
  padding: 10px;
  color: #fffcf2;
  font-size: 18px;
}
.modal_accept_svg, .modal_bad_svg {
  width: 30px;
  height: 30px;
  margin-bottom: 10px;
}
.modal_accept_svg img, .modal_bad_svg img {
  width: 100%;
}
.modal_accept_text, .modal_bad_text {
  width: 100%;
  text-align: center;
  font-size: 16px;
}